import { DATE_FORMAT } from "constants/general";
import dayjs from "dayjs";
import { useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useSearchParams } from "react-router-dom";

import { NS10Codes } from "types";

import { createProxyWithHandler } from "utils/proxy";

const useCommonInfoContextValue = () => {
	const { t } = useTranslation();
	const [search] = useSearchParams();
	const currLang = localStorage.getItem("i18nextLng");
	const fromDate = search.get("fromDate") ?? dayjs().format(DATE_FORMAT);
	const toDate = search.get("toDate") ?? dayjs().format(DATE_FORMAT);
	const page = search.get("page") ?? "0";
	const size = search.get("pageSize") ?? "10";
	const queue = search.get("queue") ?? "6000"; // undefined
	const categoryId = search.get("categoryId") ?? undefined;
	const subCategoryId = search.get("subCategoryId") ?? undefined;
	const themeUkId = search.get("themeUkId") ?? undefined;
	const fromSeconds = search.get("fromSeconds") ?? undefined;
	const toSeconds = search.get("toSeconds") ?? undefined;

	const mlnOptions = {
		sum: {
			label: t("сўм"),
			value: 1,
			num: 1
		},
		mln: {
			label: t("млн"),
			value: "mln",
			num: 1_000_000
		},
		mlrd: {
			label: t("млрд"),
			value: "mlrd",
			num: 1_000_000_000
		}
	};

	const pageSizeOptions = useMemo(
		() => [
			{ label: "10", value: "10" },
			{ label: "20", value: "20" },
			{ label: "50", value: "50" },
			{ label: "100", value: "100" }
		],
		[]
	);

	const mlnObjOptionsProxy = createProxyWithHandler(mlnOptions, mlnOptions["sum"]);

	const months: Record<number, { value: string; label: string }> = useMemo(
		() => ({
			1: { value: "1", label: t("Январь") },
			2: { value: "2", label: t("Февраль") },
			3: { value: "3", label: t("Март") },
			4: { value: "4", label: t("Апрель") },
			5: { value: "5", label: t("Май") },
			6: { value: "6", label: t("Июнь") },
			7: { value: "7", label: t("Июль") },
			8: { value: "8", label: t("Август") },
			9: { value: "9", label: t("Сентябрь") },
			10: { value: "10", label: t("Октябрь") },
			11: { value: "11", label: t("Ноябрь") },
			12: { value: "12", label: t("Декабрь") }
		}),
		[t]
	);

	const hashRegions: Record<NS10Codes, string> = {
		35: t("Қорақалпоғистон"),
		3: t("Андижон"),
		6: t("Бухоро"),
		8: t("Жиззах"),
		10: t("Қашқадарё"),
		14: t("Наманган"),
		12: t("Навоий"),
		18: t("Самарқанд"),
		22: t("Сурхондарё"),
		24: t("Сирдарё"),
		27: t("Тошкент"),
		30: t("Фарғона"),
		33: t("Хоразм"),
		26: t("Тошкент ш"),
		0: t("Республика")
	};

	const monthOptions = Object.values(months);

	return {
		state: {
			fromSeconds,
			toSeconds,
			currLang,
			themeUkId,
			categoryId,
			subCategoryId,
			queue,
			page,
			size,
			fromDate,
			toDate,
			months,
			pageSizeOptions,
			mlnOptions,
			mlnObjOptionsProxy,
			monthOptions,
			hashRegions
		},
		actions: {}
	};
};

export default useCommonInfoContextValue;
